<script>
	// import starV from '../../../components/rate-star/rate.vue'
	import starV from '../../../components/banxing/banStar.vue'
	
	import payView from '../../../components/payview/payview.vue'
	import tipMoneyV from '../../../components/addorder/order-tip.vue'
	export default {
		components:{
			starV,
			payView,
			tipMoneyV
		},
		data() {
			return {
				windowHeight: 0,
				statusBarHeight: 0,
				scrollHeight: 0,
				orderType: 0,
				orderID: '',
				orderMsg:null,
				isShowPrice:0,
				showpay: 0,
				showTip:0,
				addTipMoney:0,
				payMoney:0
			}
		},
		onLoad(option) {
			var that = this
			that.statusBarHeight = getApp().globalData.top
			var res = uni.getSystemInfoSync()
			that.windowHeight = res.windowHeight
			that.scrollHeight = that.windowHeight - that.statusBarHeight - 52
			that.orderID = option.order
			that.requestOrderDeatiles()
	
		},
		methods: {
			requestOrderDeatiles(){
				var that = this
				that.http.sendRequest('Orders.GetDetail',{'orderid':that.orderID}).then(function(res){
					if(res.code == 0){
						that.orderMsg = res.info[0]
						that.orderType = that.orderMsg.type
						console.log(that.orderMsg)
					}
				})
			},
			doBack() {
				uni.navigateBack({

				})
			},
			//订单追踪
			doOrderStateDetail(){
				uni.navigateTo({
					url:'./ordertracking?orderID=' + this.orderID
				})
			},
			//退款进度
			tuikuanjindu(){
				uni.navigateTo({
					url:'refundOrder/refundDetails?orderID=' + this.orderID
				})
			},
			onChange(e){
				
			},
			call(){
				var that = this
				uni.makePhoneCall({
					phoneNumber:that.orderMsg.rinfo.mobile
				})
			},
			showBigImage(index){
				var that = this
				uni.previewImage({
					current: index,
					urls:that.orderMsg.thumbs,
				})
			},
			priceMessage(){
				this.isShowPrice = !this.isShowPrice
			},
			copyWord(){
				var that = this
				uni.setClipboardData({
					data:that.orderMsg.orderno
				})
				uni.showToast({
					title:'复制成功',
					icon:'none'
				})
			},
			doCancleOrder(){
				var that = this
				uni.showModal({
					title: '',
					content: '是否确定取消订单',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							that.requestCancle('Orders.Cancel')
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			requestCancle(e){
				var that = this
				that.http.sendRequest(e,{'orderid':that.orderID}).then(function(res){
					if(res.code == 0){
						uni.navigateBack({
							
						})
					}
				})
			},
			
			
			doPayOrder(){
				this.addTipMoney = 0
				this.payMoney = this.orderMsg.money
				this.showpay = 1
			},
			doAddTipMoney(){
				this.showTip = 1
			},
			doApplyReturnMoney() {
				uni.navigateTo({
					url:'./refundOrder/applyRefund?orderID=' + this.orderID + '&orderno=' + this.orderMsg.orderno + '&money=' + this.orderMsg.money
				})
			
			},
			doLianxiPingtai(){
				uni.setStorage({
					key: 'weburl',
					data: getApp().globalData.service_url_kefu,
					success() {
						uni.navigateTo({
							url: '../webView/webView',
						});
					}
				})
			},
			doDelateOrder(){
				var that = this
				uni.showModal({
					title: '',
					content: '是否确定删除订单',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							that.requestCancle('Orders.Del')
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			doCancleApplyReturnMoney(){
				var that = this
				uni.showModal({
					title: '',
					content: '是否确定取消退款申请',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							that.requestCancle('Orders.Cancelrefund')
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			zailaiyidan(){
				uni.switchTab({
					url:'../index/index'
				})
			},
			doPingjia(){
				uni.navigateTo({
					url:'./orderEvaluate?orderID=' + this.orderID
				})
			},
			closePayView(e) {
				console.log(e)
				var that = this
				
				that.showpay = 0
				if (e == 0) {
			
				} else {
					var url = ''
					var dic = {
						// #ifdef APP-PLUS
						'payid': e.id,
						// #endif
						'orderid': that.orderMsg.id,
						// #ifdef MP-WEIXIN
						'openid': uni.getStorageSync('openid'),
						'payid': e.id == 2 ? '3' : e.id,
						// #endif
						
					}
					if(that.addTipMoney > 0){
						url = 'Orders.Fee'
						dic.fee = that.addTipMoney
					}else{
						url = 'Orders.Repay'
					}
					that.http.sendRequest(url, dic).then(function(res) {
						if (res.code == 0) {
							if (e.id == 1) {
								uni.requestPayment({
									provider: 'alipay',
									orderInfo: res.info[0].ali
										.orderinfo, //微信、支付宝订单数据 【注意微信的订单信息，键值应该全部是小写，不能采用驼峰命名】
									success: function(res) {
										uni.showToast({
											title:'支付成功',
											duration: 2000
										})
										console.log('success:' + JSON.stringify(res));
										if(url == 'Orders.Fee'){
											that.orderMsg.fee = that.orderMsg.fee *1 + that.addTipMoney*1
											that.orderMsg.money = that.orderMsg.money * 1 + that.addTipMoney*1
										}else{
											// that.doBack()
											that.requestOrderDeatiles()
										}
									},
									fail: function(err) {
										console.log('fail:' + JSON.stringify(err));
										uni.showToast({
											title:'支付失败',
											duration: 2000
										})
									}
								});
							} else if (e.id == 2) {
								uni.requestPayment({
									"provider": "wxpay",
									// #ifdef APP-PLUS
									"orderInfo": res.info[0].wx,
									// #endif
									// #ifdef MP-WEIXIN
									    'timeStamp': res.info[0].small.timeStamp,
									    'nonceStr': res.info[0].small.nonceStr,
									    'package': res.info[0].small.package,
									    'signType': res.info[0].small.signType,
									    'paySign': res.info[0].small.sign,
									// #endif
									
									
									// {
									//     "appid": "wx499********7c70e",  // 微信开放平台 - 应用 - AppId，注意和微信小程序、公众号 AppId 可能不一致
									//     "noncestr": "c5sEwbaNPiXAF3iv", // 随机字符串
									//     "package": "Sign=WXPay",        // 固定值
									//     "partnerid": "148*****52",      // 微信支付商户号
									//     "prepayid": "wx202254********************fbe90000", // 统一下单订单号 
									//     "timestamp": 1597935292,        // 时间戳（单位：秒）
									//     "sign": "A842B45937F6EFF60DEC7A2EAA52D5A0" // 签名，这里用的 MD5 签名
									// }
									success(res) {
										uni.showToast({
											title:'支付成功',
											duration: 2000
										})
										console.log(res)
										if(url == 'Orders.Fee'){
											that.orderMsg.fee = that.orderMsg.fee *1 + that.addTipMoney*1
											that.orderMsg.money = that.orderMsg.money * 1 + that.addTipMoney*1
										}else{
											// that.doBack()
											that.requestOrderDeatiles()
										}
									},
									fail(e) {
										console.log(e)
										uni.showToast({
											title:'支付失败',
											duration: 2000
										})
									}
								})
							} else {
								uni.showToast({
									title:'支付成功',
									duration: 2000
								})
								if(url == 'Orders.Fee'){
									that.orderMsg.fee = that.orderMsg.fee *1 + that.addTipMoney*1
									that.orderMsg.money = that.orderMsg.money * 1 + that.addTipMoney*1
								}else{
									// that.doBack()
									that.requestOrderDeatiles()
								}
								
							}
						}else{
							uni.showToast({
								title:'支付失败',
								duration: 2000
							})
						}
					
					})
					
				}
			
			},
			tipMoneyAdd(e){
				console.log(e) 
				this.showTip = 0
				if(e > 0){
					this.payMoney = e
					this.addTipMoney = e
					this.showpay = 1
				}
			}
			
		}
	}
</script>

